<template>
  <div >
    <div class="container">
      <mt-tab-container v-model="active">
        <mt-tab-container-item id="ystHome">
          <ystHome></ystHome>
        </mt-tab-container-item>
        <mt-tab-container-item id="growLine">
          <growLine></growLine>
        </mt-tab-container-item>
        <mt-tab-container-item id="message">
          <message></message>
        </mt-tab-container-item>
        <mt-tab-container-item id="teachChild">
          <teachChild></teachChild>
        </mt-tab-container-item>
        <mt-tab-container-item id="me">
          <me></me>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
    <div class="bottom-block"></div>
    <mt-tabbar v-model="active">
      <mt-tab-item id="ystHome">
        <img v-if="active == 'ystHome'" slot="icon" src="../../assets/index/icon_index_selected.png">
        <img v-else slot="icon" src="../../assets/index/icon_index_default.png">
        幼视通
      </mt-tab-item>
      <mt-tab-item id="growLine">
        <img  v-if="active == 'growLine'" slot="icon" src="../../assets/index/icon_circle_selected.png">
        <img v-else slot="icon" src="../../assets/index/icon_circle_default.png">
        成长圈
      </mt-tab-item>
      <mt-tab-item id="message">
        <img  v-if="active == 'message'" slot="icon" src="../../assets/index/icon_news_selected.png">
        <img v-else slot="icon" src="../../assets/index/icon_news_default.png">
        消息
      </mt-tab-item>
      <mt-tab-item id="teachChild">
        <img  v-if="active == 'teachChild'" slot="icon" src="../../assets/index/icon_baby_selected.png">
        <img v-else slot="icon" src="../../assets/index/icon_baby_default.png">
        育儿堂
      </mt-tab-item>
      <mt-tab-item id="me">
        <img  v-if="active == 'me'" slot="icon" src="../../assets/index/icon_user_selected.png">
        <img v-else slot="icon" src="../../assets/index/icon_user_default.png">
        我的
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
import ystHome from '../ystHome/ystHome';
import me from '../me/me';
import message from '../message/message';
import teachChild from '../teachChild/teachChild';
import growLine from '../growLine/growLine';

export default {
  data() {
    return {
      motto: '点击头像，进入',
      userInfo: {},
      active: 'ystHome',
      tabList: ['ystHome', 'growLine', 'message', 'teachChild', 'me'],
    };
  },

  components: {
    ystHome,
    me,
    message,
    teachChild,
    growLine,
  },

  methods: {
    bindViewTap() {

    },
    getUserInfo() {
      // 调用登录接口
      wx.login({
        success: () => {
          wx.getUserInfo({
            success: (res) => {
              this.userInfo = res.userInfo;
            },
          });
        },
      });
    },
    clickHandle(msg, ev) {
      console.log('clickHandle:', msg, ev);
    },
  },

  created() {
    console.log(this.$route.params.name);
  },
  mounted() {
    console.log(this.$route.query);
    if (this.tabList.indexOf(this.$route.query.tab) > 0) {
      this.active = this.$route.query.tab;
    } else {
      this.active = 'ystHome';
    }
  },
};
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .container {
    position: relative;
  }
  .bottom-block {
    height: 55px;
  }
</style>



