<template>
  <div>
    <div class="scrollView" scroll-x>
      <div class="scroll-container">
        <div class="item-container" v-for="(item,index) in itemList" @click="clickItem(item)">
          <img :src="item.imageUrl"/>
          <span>{{item.title}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    name: 'yst-scroll-bar',
    data() {
      return {
        itemList: [{
          title: '看看宝宝',
          imageUrl: require('../assets/index/index_monitor_btn_default.png'),
        },
        {
          title: '入离时间',
          imageUrl: require('../assets/index/index_time_btn_default.png'),
        },
        {
          title: '每周食谱',
          imageUrl: require('../assets/index/index_recipes_btn_default.png'),
        },
        {
          title: '教学规划',
          imageUrl: require('../assets/index/index_course_btn_default.png'),
        },
        {
          title: '宝宝请假',
          imageUrl: require('../assets/index/index_leave_btn_default.png'),
        },
        {
          title: '喂药申请',
          imageUrl: require('../assets/index/index_drug_btn_default.png'),
        }],
      };
    },
    methods: {
      clickItem(item) {
        this.$emit('navItem', { title: item.title });
      },
    },
  };
</script>

<style lang="less" scoped>
 @import "../style/common";
  .scrollView {
    .wh(7.5,1.7);
    overflow: scroll;
    .scroll-container {
      position: relative;
      .wh(10,1.7);
      display: flex;
      justify-content: space-around;
      .item-container {
        .flx-y-center;
        .wh(1.5,1.7);
        img {
          .wh(0.84,0.84);
        }
        span {
          .fontSize-six;
          color: @text-color2;
        }
      }
    }
  }
</style>



