<template>
  <div class="itm-container">
    <div class="flx-x-center">
      <img class="icon" :src="image"/>
      <span class="title">{{text}}</span>
    </div>
    <span class="more">{{more}}</span>
  </div>
</template>

<script>
  export default {
    name: 'img-text-more',
    props: [
      'image',
      'text',
      'more'],
  };
</script>

<style lang="less" scoped>
  @import "../style/common";
  .itm-container {
    .flx-x-between;
  }
  .icon {
    .wh(0.34,0.34);
  }
  .title {
    .wh(2.5,0.5);
    .line-height(0.5);
    .fontSize-three;
    .margin-left(0.06);
    color: @text-color1;
    display: block;
  }
  .more {
    .wh(2,0.5);
    text-align: right;
    .fontSize-three;
    color: @text-color2;
  }

</style>



