<template>
  <div class="news-cell-container" @click="clickToNewsDetail">
    <div class="left-content">
      <div class="news-title">
        首届足球嘉年华开始啦！阳光之下显本色，宝宝们热情高涨高高兴阳光之下显本色
      </div>
      <div class="flx-x-start">
        <div class="item-container">
          <img src="../assets/index/time_icon_default.png"/>
          <span>1分钟前</span>
        </div>
        <div class="item-container" style="margin-left: 8px">
          <img src="../assets/index/eye_icon_default.png"/>
          <span>9253</span>
        </div>
      </div>
    </div>
    <div class="right-content">
      <img src=""/>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'news-cell',
    props: ['info'],
    methods: {
      clickToNewsDetail() {
        this.$emit('clickToNewsDetail');
      },
    },
  };
</script>

<style lang="less" scoped>
  @import "../style/common";
  .news-cell-container {
    position: relative;
    .wh(7.5,1.7);
  }
  .left-content {
    float: left;
    .wh(4.7,1.6);
    .margin-left(0.20);
    .news-title {
      .textLine(2,0.3);
      .fontSize-three;
      .margin-top(0.16);
    }
    .item-container {
      .flx-x-center;
      .margin-top(0.32);
      img {
        .wh(0.24,0.24);
      }
      span {
        .text-color3;
        .fontSize-six;
        .margin-left(0.08);
      }
    }
  }
  .left-content::after{
    content: ' ';
    -webkit-transform:scale(0.5);
    transform:scale(0.5);
    position:absolute;
    border-bottom:1px solid @line-color;
    top:-50%;
    right:-50%;
    bottom:-50%;
    left:-50%;
  }
  .right-content {
    float: right;
    .wh(2.3,1.7);
    .margin-right(0.2);
    img {
      .wh(2.2,1.5);
      .margin-top(0.10);
      background-color: #26a2ff;
    }
  }


</style>



