<template>
  <div id="1233" @touchend="scroll">
    <swiper class="item1" indicator-dots="true" autoplay="true" circular="true" interval="3000">
      <swiper-item class="item1">

      </swiper-item>
      <swiper-item  class="item2">

      </swiper-item>
      <swiper-item  class="item3">

      </swiper-item>
    </swiper>
    <!--<mt-swipe :auto="4000">-->
      <!--<mt-swipe-item>1</mt-swipe-item>-->
      <!--<mt-swipe-item>2</mt-swipe-item>-->
      <!--<mt-swipe-item>3</mt-swipe-item>-->
    <!--</mt-swipe>-->
    <yst-scroll-bar v-on:navItem="navToPage"></yst-scroll-bar>
    <div class="yst-segmentation-line-view"></div>
    <div>
        <div class="imgTextMore">
          <imgTextMore :image="img1" text="育儿动态" more="查看更多"></imgTextMore>
        </div>
      <trendPreview></trendPreview>
    </div>
    <div class="yst-segmentation-line-view"></div>
    <div class="imgTextMore">
      <imgTextMore :image="img2" text="news" more="查看更多"></imgTextMore>
    </div>
    <newsCell @clickToNewsDetail="clickToNewsDetail()"></newsCell>
    <newsCell></newsCell>
    <newsCell></newsCell>
  </div>
</template>

<script>
import { navigateTo } from '../../utils/mRouter';
import ystScrollBar from '../../components/ystScrollBar';
import imgTextMore from '../../components/imgTextMore';
import trendPreview from '../../components/trend/trendPreview';
import newsCell from '../../components/newsCell';

export default {
  data() {
    return {
      info: '首届足球嘉年华开始啦！阳光之下显本色，宝宝们热情高涨首届足球嘉年华开始啦！阳光之下显本色，宝宝们热情高涨高高兴阳光之下显本色高高兴阳光之下显本色',
      index: 22,
      childTrendData: [{}, {}],
      liveTrendData: [{}],
      imageList: [1, 2, 3, 4, 5],
      img1: require('../../assets/index/index_circle_icon_disabled.png'),
      img2: require('../../assets/index/index_play_icon_disabled.png'),
      userImage: 'http://imgsrc.baidu.com/forum/w=580/sign=6ade238c064f78f0800b9afb49300a83/6cbb60d0f703918f31df8012543d269758eec47c.jpg',
    };
  },
  components: {
    ystScrollBar,
    imgTextMore,
    trendPreview,
    newsCell,
  },
  methods: {
    navToPage(item) {
      switch (item.title) {
        case '看看宝宝':
          navigateTo(
            '../yst_lookBaby/yst_lookBaby',
            'yst_lookBaby',
            { id: 123456 },
            this,
          );
          break;
        case '入离时间':
          navigateTo(
            '../yst_babyReport/yst_babyReport',
            'yst_babyReport',
            { id: 123456 },
            this,
          );
          break;
        case '宝宝请假':
          navigateTo(
            '../yst_askLeave/yst_askLeave',
            'yst_askLeave',
            { id: 123456 },
            this,
          );
          break;
        case '喂药申请':
          navigateTo(
            '../yst_feedMedicine/yst_feedMedicine',
            'yst_feedMedicine',
            { id: 123456 },
            this,
          );
          break;
        case '每周食谱':
          navigateTo(
            '../yst_babyFood/yst_babyFood',
            'yst_babyFood',
            { id: 123456 },
            this,
          );
          break;
        case '教学规划':
          navigateTo(
            '../yst_classTable/yst_classTable',
            'yst_classTable',
            { id: 123456 },
            this,
          );
          break;
        default:
          console.log('error nav');
      }
    },
    clickToNewsDetail() {
      console.log('goog');
      navigateTo(
        '../yst_news_detail/yst_news_detail',
        'yst_news_detail',
        { id: 123456 },
        this,
      );
    },
    scroll(e) {
      console.log(e.target.offsetTop);
      console.log(e.target.scrollTop);
    },
  },
  created() {
    console.log('created');
  },
  mounted() {
    console.log('mounted');
  },
};
</script>

<style lang="less" scoped>
@import '../../style/common';
.item1 {
  .wh(7.5,3.6);
  background-color: blue;
}
.item2 {
  .wh(7.5,3.6);
  background-color: red;
}
.item3 {
  .wh(7.5,3.6);
  background-color: yellow;
}
.imgTextMore {
  .margin(0.16,0.16,0,0.16);
}

</style>



